.buttons:hover {
  transform: scale(1.2);
  transition: transform 0.3s ease-out; }

.burger-main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: white; }

.main-menu_column {
  padding: 0 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.main-menu__list {
  white-space: nowrap;
  padding: 0;
  margin: 0; }

.main-menu__list a:hover {
  color: #7e7e82; }

.main-menu__list a:hover::before {
  content: '';
  position: absolute;
  height: 32px;
  width: 4px;
  background: #41f6d7;
  margin-left: -26px;
  border-radius: 0 5px 5px 0; }

.main-menu__list_items:focus::before {
  content: '';
  position: absolute;
  height: 32px;
  width: 4px;
  background: #41f6d7;
  margin-left: -26px;
  border-radius: 0 5px 5px 0; }

.main-menu__list_items:focus a {
  color: #7e7e82; }

.main-menu__list_items {
  cursor: pointer;
  border: 2px solid transparent;
  list-style: none;
  text-align: center;
  padding-bottom: 24px;
  text-decoration: none;
  margin-left: 0px;
  font-family: 'TT Lakes';
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left; }

.burger-footer {
  display: flex;
  flex-direction: column;
  align-content: flex-end; }

.lower-menu {
  display: flex;
  flex-direction: row;
  justify-content: flex-start; }

.lower-menu__button {
  margin: auto;
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  margin: 0; }

.lower-menu__button:not(.about__item:first-child) {
  margin: 0 0 0 24px; }

.footer__email {
  display: block;
  height: 24px;
  width: 106px;
  margin: 10px 0;
  margin-left: 24px; }

.footer__email_a {
  height: 24px;
  width: 106px;
  text-decoration: none;
  color: black; }

.footer__phone_a {
  height: 32px;
  width: 196px;
  text-decoration: none;
  color: black;
  font-weight: bold;
  font-size: 24px;
  margin-left: 24px; }

.lang__select {
  margin-top: 25px;
  margin-left: 24px;
  margin-bottom: 32px;
  display: flex;
  flex-direction: row;
  justify-content: start;
  padding-left: 0px; }

.select__lang_ru,
.select__lang_en,
.select__lang_ch {
  text-decoration: none;
  background-color: white;
  color: black;
  list-style: none;
  padding: 0;
  margin-left: 0px;
  padding-left: 0px;
  font-size: 16px;
  border: 0;
  cursor: pointer;
  padding-right: 5px; }

.select__item:focus a {
  color: #7e7e82; }

.lang__select a:hover {
  color: #7e7e82; }

@media screen and (min-width: 360px) {
  .burger-menu {
    min-width: 360px; } }

@media screen and (min-width: 1120px) {
  .burger-menu {
    /* opacity: 1;
    visibility: visible; */
    transform: translate(0px, 0px);
    transition: all 0s ease 0s;
    width: 320px;
    display: block;
    position: static;
    z-index: 170; }
  .burger-menu {
    min-width: 320px;
    box-shadow: none; }
  .burger-header__container {
    padding-top: 20px; }
  .burger-footer {
    padding-top: 200px; }
  .close {
    display: none; }
  .burger-menu__button_logo-img {
    margin-left: 0px; } }

